.errorContainer h1{
    position: absolute;
    top:71vh;
    font-family: 'Karla', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 3.33vw;
    line-height: 56px;
    color: #F2F2F2;
}
.errorContainer span{
    color: black;
    font-weight: bolder;
}

.errorContainer button {
    display: inline-block;
    position: absolute;
    top:85vh;
    background: #F2CB07;
    box-shadow: inset 0px -9px 0px rgba(24, 24, 24, 0.18);
    border-radius: 11px;
    width:16vw;
    height: 4.58vw;
    font-family: 'Karla', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 2.24vw;
    line-height: 27px;
    text-align: center;
    color: #212121;
    border-color:#F2CB07;
    cursor: pointer;
    outline: none;
}
.errorContainer button:hover{
    border:none;
} 
.errorContainer button:active{
    transform: translateY(4px);
    box-shadow: 0 5px (0, 0, 0, 0.18);  
    
} 

